.contacts {
  margin: 0 auto;
  // margin-top: 100px;
  padding-top: 44px;
  margin-left: -1px;
  // margin-right: 2px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  position: relative;
  // outline: 2px solid #dda0dd;
}

.contacts__title {
  grid-area: contacts__title;
  margin-bottom: 29px;
}

.contacts__address-wrapper {
  margin-bottom: 33px;
  grid-area: contacts__address-wrapper;
}

.contacts__address {
  margin: 0;
  padding-bottom: 30px;
  display: flex;
}

.contacts__address-name {
  margin: 0;
  width: 76px;
  display: block-inline;
  font-size: 14px;
  // 30/14 = 2.14
  line-height: 2.14;
  /* or 214% */
  color: $color-gray;
}

.contacts__address-value {
  margin: 0;
  margin-left: 2px;
  width: 183px;
  display: block-inline;
  font-size: 14px;
  // 30/14 = 2.14
  line-height: 2.14;
  /* or 214% */
  color: $color-gray;
}

.contacts__email {
  position: relative;
  text-decoration: none;
  color: $color-gray;

  &::after {
    position: absolute;
    content: "";
    top: 20px;
    left: 0;
    right: 0;
    height: 1px;
    background-color: $color-green;
  }

  &:hover {
    color: $color-green;
  }

  &:active::after {
    opacity: 0.3;
  }
}

.contacts__map-wrapper {
  margin: 0 -30px;
  margin-bottom: 40px;
  grid-area: contacts__map-wrapper;
}

.contacts__button {
  margin: 0 auto;
  padding-top: 3px;
  width: 260px;
  height: 52px;
  grid-area: contacts__button;
}

@media (min-width: $tablet-width) {
  .contacts {
    margin-bottom: 57px;
  }

  .contacts__title {
    margin-top: 42px;
    margin-bottom: 50px;
    font-weight: 700;
    font-size: 43px;
    line-height: 50px;
  }

  .contacts__address-wrapper {
    margin-bottom: 18px;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .contacts__address {
    grid-template-columns: 82px 257px;
  }

  .contacts__address-name {
    justify-self: flex-start;
    font-weight: 400;
    font-size: 17px;
    line-height: 30px;
  }

  .contacts__address-value {
    width: 258px;
    justify-self: flex-start;
    font-weight: 400;
    font-size: 17px;
    line-height: 30px;
  }

  .contacts__email {
    &::after {
      top: 25px;
    }
  }

  .contacts__map-wrapper {
    margin-bottom: 55px;
  }

  .contacts__button {
    width: 230px;
    font-weight: 700;
    font-size: 17px;
    line-height: 20px;
  }
}

